// ProductTable.js
import React from 'react';

const ProductTable = ({products}) => {
    let tableHead = () => {
    let header = Object.keys(products[0])
                 .map((e) => <th>{e}</th>);
    return <tr>{header}</tr>;
    }
    let tableBody = () => {
        let header = Object.keys(products[0]);
        let body = products.map(e=> {
            let tmp = []
            for (let i of header)
                if (typeof e[i] === "boolean")
                    tmp.push(<td>{e[i].toString()}</td>)
                else
                    tmp.push(<td>{e[i]}</td>)
            return <tr>{tmp}</tr>
        })
        return body;
    }
    return (
        <>
        <h1>Product List</h1>
        { (products && products.length > 0)?
        <table>
            <thead>
                {tableHead()}
            </thead>
            <tbody>
                {tableBody()}
            </tbody>
        </table>
        : <h2>Loading...</h2>
        }
        </>
    )
}
export default ProductTable